*{
    box-sizing: border-box;
    padding: 0; 
    margin: 0; 
    font-family:'Gill Sans', 'Gill Sans MT', Calibri, 'Trebuchet MS', sans-serif;
    /* border: solid;  */
}

/* --------------------------------- Header --------------------------------- */
.cart-btn>img{
    width: 2rem; 
}
.cart-btn{
    z-index: 2;
}

.navbar-container{
    display: flex; 
    justify-content: space-between;
    background-color: gold;
    
    align-items: center;
    padding: .5rem; 
    font-size: 2rem; 
}
.navbar{
    font-size: 1.2rem; 
}
/* -------------------------------------------------------------------------- */
/* ---------------------------------- Cart ---------------------------------- */
.cart-items{
    border: solid; 
    width: 30%; 

    /* position: absolute;  */
    right:  0;
    /* z-index: -1;  */

    background-color: skyblue; 

    /* visibility: hidden; */
    /* display: none; */
}

.cart-items:not([show]){
    display: none;
}
.cart-btn:not([show]){
    display: none;
}
/* [show="false"]{
    visibility: hidden;
}
[show="true"]{
    visibility: visible;
} */
.cart-item{
    /* border: 1px solid black;  */
    margin: .2rem 0; 
    background-color: white; 
    border: 2px solid 0;
}
.cart-item-display{
    border: 1px solid black; 
}

/* -------------------------------------------------------------------------- */
/* ---------------------------------- Store --------------------------------- */
.store-items{
    /* border: solid;  */
    /* background-color: lightgrey; */
    display: flex; 
    flex-wrap: wrap; 
}

.store-item{
    /* border: solid;  */
    /* height: 10rem;  */
    width: 50%; 

    font-size: 1.5rem; 
    /* flex-grow: 1;  */
}
.item-display{
    width: 80%; 
    height: 5rem;
    border: solid; 
}